import { getUserInfo } from '../../api/user'
const user = {
    state: {
        userInfo: null
    },
    getters: {},
    mutations: {
        // 设置用户信息
        setUserInfo(state, data) {
            state.userInfo = data
        }
    },
    actions: {
        /**
         * @function 获取用户信息
         */
        async getUserInfo({ commit, rootState }) {
            if (rootState.app.token) {
                const getUserInfoData = await getUserInfo()
                if (getUserInfoData.data.status == "SUCCESS") {
                    commit('setUserInfo', getUserInfoData.data.data.user)
                    return
                }
                throw new Error()
            }
        }
    },
    namespaced: true
}
export default user;