<template>
  <div id="head">
    <div class="navi">
      <ul>
        <li>
          <router-link to="/index">首页</router-link>
        </li>
        <li>
          <router-link to="/project/index">找项目</router-link>
        </li>
        <li>
          <router-link to="/capital/index">找资金</router-link>
        </li>
        <li>
          <router-link to="/vip">了解会员</router-link>
        </li>
        <li v-if="userInfo">
          <Poptip trigger="hover" title="Title" content="content">
            <Badge :dot="userInfo.remind.count!=0">
              <router-link to="/user/notice">个人中心</router-link>
            </Badge>
            <div slot="title" style="text-align: center">
              <img
                :src="imgUrl+userInfo.photo"
                alt
                style="width:75px;height:75px;border-radius: 50%;"
              />
            </div>
            <div slot="content">{{userInfo.memberName}}：{{userInfo.phone}}</div>
          </Poptip>
        </li>
        <li v-else>
          <router-link to="/login">注册/登录</router-link>
        </li>
        <li v-if="userInfo">
          <a @click="logout">退出登录</a>
        </li>
      </ul>
      <div class="cl"></div>
    </div>
  </div>
</template>
<script>
import { messages, imgUrl } from "../../../utils";
import { Poptip, Badge } from "iview";
import { mapActions, mapState } from "vuex";
export default {
  components: {
    [Poptip.name]: Poptip,
    [Badge.name]: Badge
  },
  computed: {
    ...mapState("user", ["userInfo"])
  },
  data() {
    return {
      imgUrl: imgUrl
    };
  },
  methods: {
    ...mapActions("app", ["Logout"]),
    ...mapActions("user", ["getUserInfo"]),
    logout() {
      this.Logout().then(() => {
        messages.success(`您已成功退出登录，感谢您的使用`);
        this.$router.push("/index");
      });
    }
  },
  created() {
    this.getUserInfo();
  }
};
</script>
