import { Message } from "iview";
let MessageDuration = 3 //弹窗停留时间
let baseUrl = 'http://47.98.63.219:8081'
let fileUploadUrl = 'http://47.98.63.219:8081/fileUpload'
let imgUrl = 'http://47.98.63.219:8081'
let adminUrl = 'http://47.98.63.219/maven'

/**
 * @function 数字打马
 * @param {传入数值} nub 
 */
const nubMosaic = (nub) => {
    return `${nub.substring(0, 6)}*******${nub.substring(nub.length - 4)}`
}
//自定义验证
// 验证手机
var validatePhone = (rule, value, callback) => {
    if (!value) {
        return callback(new Error());
    } else if (!/^[1][3,4,5,7,8][0-9]{9}$/.test(value)) {
        return callback(new Error());
    } else {
        callback();
    }
};
// 验证数字保留2位小数
var validateNub = (rule, value, callback) => {
    if (!value) {
        return callback(new Error());
    } else if (!/^\d+(\.\d{1,2})?$/g.test(value)) {
        return callback(new Error());
    } else {
        callback();
    }
};
// 全局提示
const messages = {
    success(msg) {
        Message.success({
            content: msg,
            duration: MessageDuration
        })
    },
    error(msg) {
        Message.error({
            content: msg,
            duration: MessageDuration
        })
    },
    warning(msg) {
        Message.warning({
            content: msg,
            duration: MessageDuration
        })
    }
}
export {
    messages,
    baseUrl,
    fileUploadUrl,
    imgUrl,
    nubMosaic,
    validateNub,
    validatePhone,
    adminUrl
}