import { loginPwd, loginCode, loginRegister } from '../../api/user'
import Cookies from "js-cookie";
const app = {
    state: {
        token: Cookies.get('token') ? Cookies.get('token') : null,
        loadingData: {
            show: false,
            loadNub: 0
        }
    },
    getters: {},
    mutations: {
        /**
         * @function 设置token
         * @param {data 提交的数据 , operation 提交操作} data 
         */
        setToken(state, data) {
            state.token = data.data
            if (data.operation == 'add') {
                //存入浏览器cookie
                Cookies.set("token", state.token, { expires: 7 });
            }
            else if (data.operation == 'remove') {
                Cookies.remove('token');
            }
        },
        /**
         * @function 加载圈状态
         * @param {* loadNub 次数 (整数++ 复数 --) } data 
         */
        loadingStatus(state, data) {
            state.loadingData.loadNub += data.loadNub
            // 如果同时发出多个请求只有 state.loadingData.loadNub = 0 时加载框消失
            state.loadingData.show = state.loadingData.loadNub == 0 ? false : true
        }
    },
    actions: {
        /**
         * @function 账号密码登录
         * @param {*提交数据 phone 手机号 password} formData 
         */
        async LoginByUsername({ commit, dispatch }, formData) {
            const loginData = await loginPwd(formData)
            if (loginData.data.status == "SUCCESS") {
                //存入state状态中
                commit('setToken', { data: loginData.data.data.token, operation: 'add' })
                await dispatch('user/getUserInfo', {}, { root: true })
                return
            }
            throw new Error()
        },
        /**
         * @function 手机验证码登录
         * @param {*提交数据 phone 手机号 code 验证码} formData 
         */
        async LoginCode({ commit, dispatch }, formData) {
            const loginData = await loginCode(formData)
            if (loginData.data.status == "SUCCESS") {
                //存入state状态中
                commit('setToken', { data: loginData.data.data.token, operation: 'add' })
                await dispatch('user/getUserInfo', {}, { root: true })
                return
            }
            throw new Error()
        },
        /**
         * @function 手机注册登录
         * @param {*提交数据 phone 手机号 password 密码 code 验证码} formData 
         */
        async LoginRegister({ commit, dispatch }, formData) {
            const loginData = await loginRegister(formData)
            if (loginData.data.status == "SUCCESS") {
                //存入state状态中
                commit('setToken', { data: loginData.data.data.token, operation: 'add' })
                await dispatch('user/getUserInfo', {}, { root: true })
                return
            }
            throw new Error()
        },
        //退出登录
        Logout({ commit }) {
            return new Promise((resolve) => {
                commit('setToken', { data: null, operation: 'remove' })
                commit('user/setUserInfo', null, { root: true })
                resolve()
            })
        }
    },
    namespaced: true

}
export default app;