import Axios from 'axios'
import { baseUrl, messages } from '../utils'
import router from '../router'
import store from '../store/index'
export default () => {
    Axios.defaults.baseURL = baseUrl
    // http request 拦截器
    Axios.interceptors.request.use(
        config => {
            if (store.state.app.token) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
                config.headers.token = store.state.app.token
            }
            //加载圈显示
            store.commit('app/loadingStatus', { loadNub: 1 })
            return config;
        },
        err => {
            return Promise.reject(err);
        });

    // http response 拦截器
    Axios.interceptors.response.use(
        response => {
            //加载圈关闭
            store.commit('app/loadingStatus', { loadNub: -1 })
            //如果请求返回 response.data.status == FAIL 则显示接口里的msg原因
            if (response.data.status == "FAIL") {
                messages.error(response.data.msg)
            }
            return response;
        },
        error => {
            //加载圈关闭
            store.commit('app/loadingStatus', { loadNub: -1 })
            //判断接口status状态码是否有跟下面匹配的状态码对应
            switch (error && error.response.status) {
                case 4000: //token过期
                    store.dispatch('app/Logout')
                    messages.error('您的登录信息失效,请重新登录')
                    router.push('/login')
                    break;
                case 5000: //用户没有权限查看
                    messages.warning(`抱歉,您访问的项目/资金正在准备中,敬请期待`)
                    router.push('/index')
                    break;
                case 5001: //找不到对应数据
                    messages.warning(`抱歉,没有找到对应项目/资金数据`)
                    router.push('/index')
                    break;
                default:
                    messages.error('未知错误,请联系华联投管理员')
                    router.push('/index')
                    break;
            }

            return Promise.reject(error)   // 返回接口返回的错误信息
        });
}